import {Util} from '../commontypes/Util';

/**
 * @class FilterParameter
 * @deprecatedclass SuperMap.FilterParameter
 * @category iServer Data FeatureResults
 * @classdesc 
 * @param {Object} options - 
 * @param {string} options.attributeFilter - 
 * @param {string} options.name - 
 * @param {Array.<JoinItem>} [options.joinItems] -  JoinItem 
 * @param {Array.<LinkItem>} [options.linkItems] -  LinkItem 
 * @param {Array.<string>} [options.ids] -  id  SmID 
 * @param {string} [options.orderBy] - orderBy 
 * @param {string} [options.groupBy] - 
 * @param {Array.<string>} [options.fields] - 
 * @usage
 */
export class FilterParameter {


    constructor(options) {
        /**
         * @member {string} FilterParameter.prototype.attributeFilter
         * @description 
         *  SQL  WHERE WHERE <>
         * attributeFilter 
         *  attributeFilter = ""
         *  fieldValue 100 attributeFilter = "fieldValue < 100"
         *  name  attributeFilter = "name like '%%'"
         */
        this.attributeFilter = null;

        /**
         * @member {string} FilterParameter.prototype.name
         * @description 
         * 
         * @
         * 
         * 
         * 
         * 
         */
        this.name = null;

        /**
         * @member {Array.<JoinItem>} [FilterParameter.prototype.joinItems]
         * @description  JoinItem 
         */
        this.joinItems = null;

        /**
         * @member {Array.<LinkItem>} [FilterParameter.prototype.linkItems]
         * @description  LinkItem 
         */
        this.linkItems = null;

        /**
         * @member {Array.<string>} [FilterParameter.prototype.ids]
         * @description  id  SmID 
         */
        this.ids = null;

        /**
         * @member {string} [FilterParameter.prototype.orderBy]
         * @description orderBy
         *  SQL  ORDER BY ORDER BY <>
         *  SuperMap 
         *  orderBy = ""
         *  orderBy = "1, 2"
         * SmAreapop_1994
         * 1994
         *  orderBy = "pop_1994"
         *  orderBy = "SmArea, pop_1994"
         */
        this.orderBy = null;


        /**
         * @member {string} [FilterParameter.prototype.groupBy]
         * @description 
         *  SQL  GROUP BY GROUP BY <>
         *  SuperMap 
         *  groupBy = ""
         *  groupBy = "1, 2"
         * ContinentCountry
         * 
         *   groupBy = "Country"
         *  groupBy = "Continent, Country"
         */
        this.groupBy = null;

        /**
         * @member {Array.<string>} [FilterParameter.prototype.fields]
         * @description 
         */
        this.fields = null;
        if (options) {
            Util.extend(this, options);
        }

        this.CLASS_NAME = "SuperMap.FilterParameter";
    }


    /**
     * @function FilterParameter.prototype.destroy
     * @description 
     */
    destroy() {
        var me = this;
        me.attributeFilter = null;
        me.name = null;
        if (me.joinItems) {
            for (let i = 0, joinItems = me.joinItems, len = joinItems.length; i < len; i++) {
                joinItems[i].destroy();
            }
            me.joinItems = null;
        }
        if (me.linkItems) {
            for (let i = 0, linkItems = me.linkItems, len = linkItems.length; i < len; i++) {
                linkItems[i].destroy();
            }
            me.linkItems = null;
        }
        me.ids = null;
        me.orderBy = null;
        me.groupBy = null;
        me.fields = null;
    }


}
