import 'ol/ol.css';
import Map from 'ol/Map';
import View from 'ol/View';
import TileLayer from 'ol/layer/Tile';
import VectorSource from 'ol/source/Vector';
import VectorLayer from 'ol/layer/Vector';
import GeoJSON from 'ol/format/GeoJSON';
import Polygon from 'ol/geom/polygon';
import { TileSuperMapRest, GetFeaturesByBufferParameters, FeatureService } from '@supermap/iclient-ol';
import { Feature } from 'ol';
import Style from 'ol/style/style';
import Stroke from 'ol/style/Stroke';
import Fill from 'ol/style/Fill';

const baseUrl = "https://iserver.supermap.io/iserver/services/map-world/rest/maps/World";
const dataUrl = "https://iserver.supermap.io/iserver/services/data-world/rest/data";

const map = new Map({
  target: 'map', //  ID
  view: new View({
      center: [-10, 15], // 
      zoom: 4, //    / 
      projection: 'EPSG:4326' // 
  })
});

const layer = new TileLayer({
      source: new TileSuperMapRest({
          url: baseUrl, //SuperMap iServer   
          wrapX: true //   
      }),
      projection: 'EPSG:4326' // 
});

map.addLayer(layer);

query();

function query(){

    // buffer 
    var polygon = new Polygon([[[-20, 20], [-20, -20], [20, -20], [20, 20], [-20, 20]]]);
    
    var polygonSource = new VectorSource({
        features: [new Feature(polygon)],
        wrapX: false
    });

    var polygonLayer = new VectorLayer({
        source: polygonSource,
        style: new Style({
            stroke: new Stroke({
                color: 'blue',
                width: 3
            }),
            fill: new Fill({
                color: 'rgba(0, 0, 255, 0.1)'
            })
        })
    });

    //buffer  map 
    map.addLayer(polygonLayer);


    //
    var param = new GetFeaturesByBufferParameters({
        datasetNames: ["World:Capitals"], //[Datasource Name:Dataset Name]
        bufferDistance: 30,
        geometry: polygon
    });

    new FeatureService(dataUrl).getFeaturesByBuffer(param, function (serviceResult) {
        //
        var vectorSource = new VectorSource({
            features: (new GeoJSON()).readFeatures(serviceResult.result.features),
            wrapX: false
        });

        // 
        var resultLayer = new VectorLayer({
            source: vectorSource
        });

        map.addLayer(resultLayer);
    });
}