import Observable from 'ol/Observable';

/**
 * @class ServiceBase
 * @category iServer Core
 * @classdesc ol.supermap 
 * @param {string} url - 
 * @param {Object} options - 
 * @param {string} [options.proxy] - 
 * @param {boolean} [options.withCredentials=false] -  cookie
 * @param {boolean} [options.crossOrigin] - 
 * @param {Object} [options.headers] - 
 * @extends {ol.Observable}
 * @usage
 */
export class ServiceBase extends Observable {
    constructor(url, options) {
        super(url, options);
        this.options = options || {};
        this.url = url;
        this.dispatchEvent({type: 'initialized', value: this});
    }
}
